//-------------------crop image----------------------------------//
.crop {
  @include breakpointMaker {
    @include crop;
  }
}

//-------------------content = data-content ----------------------------------//
.content-before::before {
  content: attr(data-content);
}
.content-after::after {
  content: attr(data-content);
}
// data-content="data"

//-------------------opacity----------------------------------//
.opacity {
  @for $i from 0 through 10 {
    &-#{$i} {
      filter: alpha(opacity=($i * 10)); /* internet explorer */
      -khtml-opacity: ($i/ 10); /* khtml, old safari */
      -moz-opacity: ($i/ 10); /* mozilla, netscape */
      opacity: ($i/ 10); /* fx, safari, opera */
    }
  }
}

//-------------------float----------------------------------//
.float {
  @include breakpointMaker {
    &-right {
      float: right;
    }
    &-left {
      float: left;
    }
  }
}
//-------------------clear----------------------------------//

.clear {
  @include breakpointMaker {
    @each $value in $clear {
      &-#{$value} {
        clear: $value;
      }
    }
  }
}
//-------------------object fit----------------------------------//
.fit,
.object {
  @include breakpointMaker {
    @each $value in $object-fit {
      &-#{$value} {
        object-fit: $value;
      }
    }
  }
}
//-------------------overflow----------------------------------//
.overflow,
.ow {
  @include breakpointMaker {
    @each $value in $overflow {
      &-#{$value} {
        overflow: $value;
      }
    }
  }
}
//------------------visibility----------------------------------*/

.visibility,
.v {
  @include breakpointMaker {
    @each $value in $visibility {
      &-#{$value} {
        visibility: $value;
      }
    }
  }
}

//-------------------display----------------------------------//
.d,
.display {
  @include breakpointMaker {
    @each $play in $display {
      @include breakpointMaker {
        &-#{$play} {
          display: $play;
        }
      }
    }
  }
}
