//-------------------backgrounds----------------------------------//

.bg {
  &-transparent,
  &-t {
    background-color: transparent;
  }
  &-inherit,
  &-i {
    background-color: inherit;
  }

  // @include breakpointMaker {
  //   @each $key, $value in $colors {
  //     &-#{"" + $key} {
  //       @include bg-($key);
  //       &-hover {
  //         @include bg-($key, hover);
  //         &-t:hover {
  //           @include bg-($key, $text: true);
  //         }
  //       }
  //       &-t {
  //         @include bg-($key, $text: true);
  //       }
  //     }
  //   }
  // }

  @include breakpointMaker {
    @each $key, $value in $colors {
      &-#{"" + $key} {
        @include bg-($key);
        //------------------------------------------*/
        &-hover {
          // $algo: #{$key}-dark;
          // @if (str-index(quote(#{$key}), "-light")) {
          //   $algo: str-replace($key, "-light");
          // } @else if (str-index(quote(#{$key}), "-dark")) {
          //   $algo: str-replace($key, "-dark");
          // }
          // @include bg-($key, $hover: $algo);
          @include bg-($key);
          &:hover {
            background-color: darken($value, $darken);
          }
        }
        //------------------------------------------*/
        $algo: #{&};
        $algo: erase($algo, " ");
        $algo: erase($algo, ".");
        $algo: erase($algo, "#");

        @at-root .hover\:#{$algo}:hover {
          @include bg-($key);
        }
        //------------------------------------------*/
        &-t {
          @if (lightness(Color($key)) > 40) {
            @include bg-($key, $text: dark);
          } @else {
            @include bg-($key, $text: light);
          }
          //------------------------------------------*/
          $algo: #{&};
          $algo: erase($algo, " ");
          $algo: erase($algo, ".");
          $algo: erase($algo, "#");

          @at-root .hover\:#{$algo}:hover {
            @if (lightness(Color($key)) > 40) {
              @include bg-($key, $text: dark);
            } @else {
              @include bg-($key, $text: light);
            }
          }
        }
      }
    }
  }

  // --------------
  // @each $key, $value in $colors {
  //   &-#{"" + $key} {
  //     @include bg-($value);
  //   }
  // }

  &-none,
  &-0 {
    background-color: transparent;
  }
}
